export default {
	data() {
		return {
			
		}
	},
	methods: {
		toOutCloseBox(e) {
			let sign = e.target.dataset.sign
			if(sign) {
				this.$emit('toCloseSizeBox', {status: false, sizeData: null})
			}
		},
		toSureSelectSize() {
			this.$emit('toCloseSizeBox', {status: false, sizeData: {}})
		}
	}
}
